.contacts-btn {
  width: 196px;
  height: 42px;
  color: #131313;
  text-align: center;
  cursor: pointer;
  background: #f8d24e;
  border: none;
  border-radius: 10px;
  justify-content: center;
  align-items: center;
  gap: 4px;
  font-family: Montserrat;
  font-size: 14px;
  font-style: normal;
  font-weight: 600;
  line-height: 1.1;
  transition-property: color, background, fill;
  transition-duration: .3s;
  transition-timing-function: cubic-bezier(.4, 0, .2, 1);
  display: flex;
}

.contacts-btn:hover {
  color: #131313;
}

.contacts-btn:focus {
  color: #fff;
  fill: #fff;
  background: #0a8817;
}

.contacts-btn:disabled {
  color: #131313;
  background: #ababab;
